"use client";
import React, { useCallback, useEffect, useMemo, useRef, useState } from "react";
import { useLocation } from "react-router-dom";

import videoGluteos from "../img/entregable_-_aumento_de_glúteos_(4k)_v1 (1440p).mp4";
import videoFacial from "../img/entregable_-_facial_(4k)_v1 (1440p).mp4";
import videoNoQuirurgico from "../img/entregable_-_no_quirúrgico_(4k)_v1 (1080p).mp4";
import videoContorno from "../img/Contorno corporal (1080p 60fps).compressed.mp4";
import MarcaAgua from "../img/Recurso 1.png";

const normalize = (value) =>
  (value || "")
    .toLowerCase()
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, "")
    .replace(/[^a-z0-9\s]/g, " ")
    .replace(/\s+/g, " ")
    .trim();

const videos = [
  {
    title: "Aumento de glúteos",
    description: "Indicaciones y cuidados clave para tu proceso.",
    src: videoGluteos,
    matches: ["gluteo", "gluteoplastia"],
  },
  {
    title: "Facial",
    description: "Guía rápida para aprovechar tu tratamiento.",
    src: videoFacial,
    matches: [
      "facial",
      "blefaroplastia",
      "bichectomía",
      "rinoseptoplastia",
      "papada",
      "otoplastia",
      "lifting facial",
      "estética facial",
    ],
  },
  {
    title: "Aumento de mamas",
    description: "Indicaciones clave para tu procedimiento.",
    src: videoContorno,
    matches: [
      "aumento de mamas",
      "mamoplastia",
      "ginecomastia",
      "glándulas mamarias",
    ],
  },
  {
    title: "Contorno corporal",
    description: "Indicaciones clave para contorno y recuperación.",
    src: videoContorno,
    matches: [
      "contorno",
      "lipoescultura",
      "abdominoplastia",
      "mini abdominoplastia",
      "braquioplastia",
      "cruroplastia",
      "labioplastia",
      "himen",
      "monte de venus",
      "pantorrillas",
      "lifting de brazos",
      "lifting de muslos",
    ],
  },
  {
    title: "No quirúrgico",
    description: "Recomendaciones generales antes y después.",
    src: videoNoQuirurgico,
    matches: [
      "toxina",
      "hialurónico",
      "láser",
      "cicatrices",
      "lunares",
      "tumores",
      "lipomas",
      "no quirúrgico",
    ],
  },
];

export default function VideosExplicativos() {
  const location = useLocation();
  const [procedure, setProcedure] = useState(() => {
    const state = location.state || {};
    return {
      procedimiento: state.procedimiento || "",
      procedimientoOtro: state.procedimientoOtro || "",
    };
  });

  useEffect(() => {
    if (procedure.procedimiento) return;
    try {
      const saved = JSON.parse(
        window.localStorage.getItem("ea_video_procedimiento") || "{}"
      );
      if (saved.procedimiento) {
        setProcedure({
          procedimiento: saved.procedimiento || "",
          procedimientoOtro: saved.procedimientoOtro || "",
        });
      }
    } catch (error) {
      console.error("No se pudo leer el procedimiento para videos:", error);
    }
  }, [procedure.procedimiento]);

  const rawProcedure =
    procedure.procedimiento === "Otro"
      ? procedure.procedimientoOtro
      : procedure.procedimiento;
  const normalizedProcedure = normalize(rawProcedure);

  const selectedVideo = useMemo(() => {
    if (!normalizedProcedure) return null;
    return videos.find((video) =>
      video.matches.some((match) => normalizedProcedure.includes(match))
    );
  }, [normalizedProcedure]);

  const videoRef = useRef(null);
  const [audioEnabled, setAudioEnabled] = useState(false);

  const tryAutoplay = useCallback(() => {
    const videoEl = videoRef.current;
    if (!videoEl) return;
    videoEl.muted = !audioEnabled;
    videoEl.volume = 1;
    const playPromise = videoEl.play();
    if (playPromise && typeof playPromise.catch === "function") {
      playPromise.catch(() => {});
    }
  }, [audioEnabled]);

  const enableAudio = useCallback(() => {
    setAudioEnabled(true);
    const videoEl = videoRef.current;
    if (!videoEl) return;
    videoEl.muted = false;
    videoEl.volume = 1;
    const playPromise = videoEl.play();
    if (playPromise && typeof playPromise.catch === "function") {
      playPromise.catch(() => {});
    }
  }, []);

  useEffect(() => {
    if (!selectedVideo) return;
    tryAutoplay();
  }, [selectedVideo, tryAutoplay]);

  useEffect(() => {
    if (!selectedVideo || audioEnabled) return;
    const handlePointer = () => {
      enableAudio();
    };
    window.addEventListener("pointerdown", handlePointer, { once: true });
    return () => window.removeEventListener("pointerdown", handlePointer);
  }, [selectedVideo, audioEnabled, enableAudio]);

  return (
    <section className="section section--watermark videos-explicativos">
      <div className="section__watermark" aria-hidden="true">
        <img src={MarcaAgua} alt="" loading="lazy" decoding="async" />
      </div>
      <div className="container videos-explicativos__container">
        <div className="videos-explicativos__grid">
          <div className="videos-explicativos__text">
            <header className="videos-explicativos__header">
              <h2 className="h2-contact videos-explicativos__title">
                Vídeos explicativos
              </h2>
              <p className="lead videos-explicativos__lead">
                Encuentra aquí los vídeos con indicaciones del Dr. Santillán.
              </p>
              <div className="videos-explicativos__notice">
                <span className="videos-explicativos__notice-icon" aria-hidden>
                  <svg viewBox="0 0 24 24" focusable="false" aria-hidden="true">
                    <path
                      d="M1 21h22L12 2 1 21zm12-3h-2v-2h2v2zm0-4h-2v-4h2v4z"
                      fill="currentColor"
                    />
                  </svg>
                </span>
                <div className="videos-explicativos__notice-text">
                  <div>
                    <strong>Importante:</strong> esta es la única vez que puedes ver estos vídeos.
                  </div>
                  <div className="muted">Material exclusivo para clientes.</div>
                </div>
              </div>
            </header>

            <div className="muted videos-explicativos__meta">
              Procedimiento seleccionado: {rawProcedure || "No especificado"}
            </div>

            <div className="videos-explicativos__info-card">
              <div className="videos-explicativos__info-row">
                <span className="videos-explicativos__info-icon" aria-hidden>
                  <svg viewBox="0 0 24 24" focusable="false" aria-hidden="true">
                    <circle cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="2" fill="none" />
                    <path d="M12 7v10M7 12h10" stroke="currentColor" strokeWidth="2" strokeLinecap="round" />
                  </svg>
                </span>
                <div className="videos-explicativos__info-line">
                  <span className="videos-explicativos__info-label">Procedimiento seleccionado:</span>
                  <span className="videos-explicativos__info-value">
                    {rawProcedure || "No especificado"}
                  </span>
                </div>
              </div>
              <div className="videos-explicativos__info-body">
                {selectedVideo ? (
                  <>
                    <div className="videos-explicativos__video-title">{selectedVideo.title}</div>
                    <p className="muted videos-explicativos__description">
                      {selectedVideo.description}
                    </p>
                  </>
                ) : (
                  <div className="videos-explicativos__empty">
                    <strong>No encontramos un vídeo asignado a este procedimiento.</strong>
                    <div className="muted">
                      Si necesitas ayuda, escríbenos y lo habilitamos.
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>

          <div className="videos-explicativos__media">
            {selectedVideo ? (
              <article className="videos-explicativos__card">
                <div className="videos-explicativos__frame">
                  <video
                    ref={videoRef}
                    src={selectedVideo.src}
                    controls
                    autoPlay
                    muted={!audioEnabled}
                    preload="metadata"
                    playsInline
                    onLoadedMetadata={tryAutoplay}
                    className="videos-explicativos__video"
                  />
                  {!audioEnabled && (
                    <button
                      type="button"
                      className="videos-explicativos__audio-toggle"
                      onClick={enableAudio}
                      aria-label="Activar audio del vídeo"
                    >
                      Activar audio
                    </button>
                  )}
                </div>
                <div className="videos-explicativos__details videos-explicativos__details--mobile">
                  <div className="videos-explicativos__video-title">{selectedVideo.title}</div>
                  <p className="muted videos-explicativos__description">
                    {selectedVideo.description}
                  </p>
                </div>
              </article>
            ) : (
              <div className="videos-explicativos__empty">
                <strong>No encontramos un vídeo asignado a este procedimiento.</strong>
                <div className="muted">
                  Si necesitas ayuda, escríbenos y lo habilitamos.
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </section>
  );
}
